.component {
    width: 85%;
    display: flex;
    flex-direction: column;
    padding-bottom: 100px;
    padding-top: 100px;
}

.nameProject {
    /*border: 1px solid;*/
    padding-top: 20px;
    display: flex;
    align-items: center;
    flex-direction: column;
    font-family: 'Poppins', sans-serif;
    line-height: 1.2;
    letter-spacing: 5px;
    font-weight: 600;
    font-size: 36px;
    text-transform: capitalize;
}

.myProject {
    display: flex;
    justify-content: space-around;
    height: 100%;
    flex-wrap: wrap;
}

.blockLine {
    height: 2px;
    width: 200px;
    margin: 30px 0;
    background-color: #3a8799;
}

